import './App.css';
import Transaction from './components/Transaction';
import FormComponent from './components/FormComponent';
import { useState,useEffect,/*useReducer*/ } from 'react';
import DataContext from './data/DataContext';
import ReportComponent from './components/ReportComponent';
import { BrowserRouter as Router,Switch,Route,Link} from 'react-router-dom';




function App() {
  //const Title =()=><h1 style={{color:'blue',textAlign:'center',fontSize:'1.5rem'}}>ข้อมูลรายรับ-รายจ่าย</h1>
  const desigh = {color:'blue',textAlign:'center',fontSize:'1.5rem'}
  // const initState = [
  //   {id:1,title:"ค่าเช่า",amount:-5000},
  //   {id:2,title:"ค่ากิน",amount:-3000},
  //   {id:3,title:"เงินเดือน",amount:20000},
  //   {id:4,title:"ค่าเดินทาง",amount:-3000}
  // ]
  const [items,setItems] = useState([])
  const [reportIncome,setReportIncome] = useState(0)
  const [reportExpense,setReportExpense] = useState(0)
  const onAddNewItem =(newItem)=>{
    setItems((prevItem)=>{
      return [newItem,...prevItem]
    })
    //console.log("ข้อมูลจาก Form Component = ",newItem)
  }
  useEffect(()=>{
    const amounts = items.map(items=>items.amount)
    const income = amounts.filter(element=>element>0).reduce((total,element)=>total+=element,0)
    const expense = (amounts.filter(element=>element<0).reduce((total,element)=>total+=element,0))*-1
    setReportExpense(expense.toFixed(2)/*ทดสนิยม 2 ตำแหน่ง*/)
    setReportIncome(income.toFixed(2))
    //console.log(income)
    //console.log(expense)
    //console.log(amounts)
  },[items,reportExpense,reportIncome])

  //reducer state
  // const [showReport,setShowReport] = useState(false)
  // const reducer = (state,action)=>{
  //   switch(action.type){
  //     case "SHOW" :
  //       return setShowReport(true)
  //       case "HIDE" :
  //         return setShowReport(false)
  //   }
  // }
  // const [result,dispatch] = useReducer(reducer,showReport)
  return (
   <DataContext.Provider value={{income: reportIncome,expense: reportExpense}}>
     <div className="container">
          {/* <Title/> */}
          <h1 style={desigh}>แอพบัญชีรายรับ - รายจ่าย</h1>
          <Router>
          <div>
            <ul className="horizontal-menu">
              <li>
                <Link to="/">ข้อมูลบัญชี</Link>
              </li>
              <li>
                <Link to="/insert">บันทึกข้อมูล</Link>
              </li>
            </ul>
            <Switch>
              <Route path="/" exact>
                <ReportComponent/>
              </Route>
              <Route path="/insert">
                <FormComponent onAddItem={onAddNewItem}/>
                <Transaction items = {items}/>
              </Route>
            </Switch>
          </div>
          </Router>
          {/*showReport && <ReportComponent/>*/}
          {/* <h1>{result}</h1>
            <button onClick={()=>dispatch({type:"SHOW"})}>แสดง</button>
            <button onClick={()=>dispatch({type:"HIDE"})}>ซ่อน</button> */}
     </div>
   </DataContext.Provider>

 
  );
}

export default App;
